@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
#wrapper {
  display: grid;
  grid-template-rows: 1fr 5fr;
  height: 100vh;
}

#title {
  font: bold 54px "Open Sans", sans-serif;
  text-align: center;
  margin: auto 0;
}

.nav {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: auto;
}

.nav__fragment {
  position: relative;
}

.nav__fragment__bg {
  width: 100%;
  height: 100%;
  opacity: 0.3;
  overflow: hidden;
  border-radius: 40px 40px 0 0;
}

.nav__fragment__bg.about {
  background: url(/static/media/question-marks.af4f46be.jpg) no-repeat center center / cover;
}

.nav__fragment__bg.database {
  background: url(/static/media/yerba.0359d372.jpg) no-repeat center center / cover;
}

.nav__fragment__bg.api {
  background: url(/static/media/server-room.ece56028.jpg) no-repeat center center / cover;
}

.nav__fragment--text {
  font: bold 18px "Open Sans", sans-serif;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  background: rgba(222, 184, 135, 0.6);
  border-radius: 14px;
  padding: 24px 16px;
  margin: 0 46px;
  text-align: center;
  cursor: pointer;
  transition:
    0.4s background,
    0.45s box-shadow;
}

.nav__fragment--text:hover {
  background: rgba(222, 184, 135, 0.9);
  box-shadow: 0 0 18px black;
}

.nav__fragment--title {
  margin-bottom: 10px;
}

.about--title {
  font: bold 40px "Open Sans", sans-serif;
}

.about--p {
  font: 18px "Open Sans", sans-serif;
  margin: 20px 0;
}

.database--title {
  margin-bottom: 10px;
}

.database--li {
  margin-bottom: 2px;
}

/* Globals */
* {
  padding: 0; margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans";
}

body {
  width: 100vw; height: 100vh;
  background: burlywood;
}

a {
  text-decoration: none;
  color: #000;
  color: initial;
}

.link {
  color: darkorchid;
}

.center-div {
  width: 100vw;
  text-align: center;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
}

